<template>
  <div class="comment-container">
    <el-card class="box-card">
      <!--  -->
      <div slot="header" class="clearfix">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <!-- / -->
      <!--  -->
      <el-table
        :data="comments"
        v-loading="loading"
        style="width: 100%">
        <el-table-column
          prop="title"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="total_comment_count"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="fans_comment_count"
          label="">
        </el-table-column>
        <el-table-column
          prop="comment_status"
          label=""
        >
          <template slot-scope="scope">
           {{scope.row.comment_status ? '' : ''}}
          </template>
        </el-table-column>
        <el-table-column
        label="">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.comment_status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :disabled="scope.row.loading"
              @change="changeStatus(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
      </el-table>
      <!-- / -->
      <!--  -->
      <el-pagination
        class="page"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :current-page.sync="page"
        :page-sizes="[10, 20, 30, 50]"
        :page-size.sync="perPage"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount"
        :disabled="loading"
      >
      </el-pagination>
      <!-- / -->
    </el-card>
  </div>
</template>

<script>
import {
  getArticle,
  updateCommentStatus
} from '@/api/article'
export default {
  name: 'CommentIndex',
  props: {},
  components: {},
  data () {
    return {
      comments: [], // 
      totalCount: 0, // 
      page: 1, // 
      perPage: 10, // 
      loading: false
    }
  },
  computed: {},
  watch: {},
  created () {
    this.onloadComments(1)
  },
  methods: {
    onloadComments (page = 1) {
      // 
      this.loading = true
      this.page = page
      getArticle({
        page,
        per_page: this.perPage,
        response_type: 'comment'
      }).then(res => {
        console.log(res)
        const { results, total_count: totalCount } = res.data.data
        results.forEach(article => {
          article.loading = false
        })
        this.comments = res.data.data.results
        this.totalCount = totalCount
        // 
        this.loading = false
      }).catch(err => {
        console.log('' + err)
        // 
        this.loading = false
      })
    },
    changeStatus (row) {
      // loading
      row.loading = true
      // 
      updateCommentStatus(row.id.toString(), row.comment_status).then(res => {
        this.$message({
          type: 'success',
          message: row.comment_status ? '' : ''
        })
        // loading
        row.loading = false
      })
    },
    handleSizeChange () {
      this.onloadComments()
    },
    handleCurrentChange () {
      this.onloadComments(this.page)
    }
  },
  mounted () {}
}
</script>
<style lang='less' scoped>
.page {
  margin-top: 20px;
}
</style>
