<template>
  <div class="image-container">
    <el-card class="box-card">
      <div style="padding-bottom:20px" class="image-flex">
        <el-radio-group
         v-model="collect"
         size="medium"
         :disabled="loading"
         @change="loadImages()"
        >
          <el-radio-button
           :label="false"
          ></el-radio-button>
          <el-radio-button
           :label="true"
          ></el-radio-button>
        </el-radio-group>
        <el-button
         v-if="isShowAdd"
         type="success"
         size="medium"
         :disabled="loading"
         @click="dialogVisible = true"
        ></el-button>
      </div>
      <!--  -->
      <el-row :gutter="20" v-model="images" v-loading="loading">
        <el-col :xs="12" :sm="6" :md="6" :lg="4"
          class="img-box"
          v-for="(img, index) in images"
          :key="index"
          @click.native="selected = index"
        >
          <el-image
          style="height: 100px"
          :src="img.url"
          fit="cover"
          lazy
          ></el-image>
          <div
            class="selected"
            v-if="isShowSelected && selected === index"
          ></div>
          <div v-if="isShowAction" class="img-mask">
            <!--  -->
            <!-- <span
              class="el-icon-star-off icon-xing"
              :style="img.is_collected ? 'color:red' : 'color:white'"
              @click="isCollect(img)"
            ></span> -->
            <el-button
              class="el-icon-star-off icon"
              :class="{red: img.is_collected}"
              circle
              size="mini"
              :loading="img.isLoading"
              @click="isCollect(img)"
            ></el-button>
            <!--  -->
            <el-button
              class="el-icon-delete icon-del icon"
              circle
              :loading="img.isLoading"
              @click="delImage(img)"
            ></el-button>
          </div>
        </el-col>
      </el-row>
      <!-- / -->

      <!--  -->
      <el-pagination
        class="image-pag"
        background
        layout="prev, pager, next"
        :total="totalCount"
        :page-size="pageSize"
        :current-page.sync="currentPage"
        :disabled="loading"
        @current-change="onCurrentChange"
      >
      </el-pagination>
      <!-- / -->
    </el-card>

    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%"
      :append-to-body="true"
    >
      <el-upload
        class="upload-demo"
        drag
        name="image"
        action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
        :headers="uploadHeaders"
        :on-success="onUploadSuccess"
        multiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
      </el-upload>
    </el-dialog>
  </div>
</template>

<script>
// 
import {
  getImages,
  isCollectImage,
  delImage
} from '@/api/image.js'
export default {
  name: 'ImageList',
  props: {
    // 
    isShowAdd: {
      type: Boolean, // 
      default: true // 
    },
    // 
    isShowAction: {
      type: Boolean,
      default: true
    },
    // 
    isShowSelected: {
      type: Boolean,
      default: false
    }
  },
  components: {},
  data () {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      images: [], // 
      collect: false, // 
      dialogVisible: false, // 
      uploadHeaders: {
        Authorization: `Bearer ${user.token}`
      },
      pageSize: 6, // 
      totalCount: 0, // 
      currentPage: 1, // 
      loading: false, // 
      selected: null // 
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadImages(1)
  },
  methods: {
    loadImages (page = 1, collect = false) {
      // 
      this.loading = true
      this.currentPage = page
      getImages({
        page,
        per_page: this.pageSize,
        collect: this.collect
      }).then(res => {
        const { results, total_count: totalCount } = res.data.data
        results.forEach(img => {
          img.isLoading = false
        })
        this.images = results
        this.totalCount = totalCount
        // 
        this.loading = false
      })
    },
    onUploadSuccess () {
      // 
      this.dialogVisible = false
      // 
      this.loadImages(this.currentPage, false)
    },
    onCurrentChange (page) {
      // 
      this.loadImages(page)
    },
    isCollect (img) {
      img.isLoading = true
      isCollectImage(!img.is_collected, img.id).then(res => {
        this.$message({
          message: `${!img.is_collected ? '' : ''}`,
          type: 'success'
        })
        // 
        img.is_collected = !img.is_collected
        img.isLoading = false
      }).catch(err => {
        img.isLoading = false
        console.log('' + err)
        this.$message.error('')
      })
    },
    delImage (img) {
      img.isLoading = true
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        delImage(img.id).then(res => {
          this.$message({
            message: res.message,
            type: 'success'
          })
          // 
          this.loadImages(this.currentPage)
          img.isLoading = false
        }).catch(err => {
          console.log('' + err)
          this.$message.error('')
        })
      }).catch(() => {
        img.isLoading = false
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  },
  mounted () {}
}
</script>
