<template>
  <div class="article-container">
    <el-card class="box-card first-card">
      <div slot="header" class="clearfix">
        <!--  -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item to="/"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!-- / -->
      </div>
      <!--  -->
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-radio-group v-model="status">
            <el-radio :label="null"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="4"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="channelId" placeholder="">
            <el-option
            label=""
            :value="null"
            ></el-option>
            <el-option
            v-for="(channel, index) in channels"
            :key="index"
            :label="channel.name"
            :value="channel.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="rangeDate"
            type="datetimerange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            >
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button
          type="primary"
          :disabled="isLoading"
          @click="loadArticles(1)"></el-button>
        </el-form-item>
      </el-form>
      <!-- / -->
    </el-card>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>{{totalCount}}</span>
      </div>
      <!--  -->
      <!-- Element  Loading v-loadingBoolean -->
      <el-table
      :data="articles"
      stripe
      v-loading="isLoading"
      style="width: 100%">
        <el-table-column
          prop="date"
          label="">
          <template slot-scope="scope">
          <el-image
           style="width: 100px; height: 100px"
           :src="scope.row.cover.images[0]"
           fit="cover">
           <div slot="placeholder" class="image-slot">
             <span class="dot">...</span>
           </div>
           </el-image>
            <!-- <img v-if="scope.row.cover.images[0]" :src="scope.row.cover.images[0]"
            class="article-cover"
            alt="">
            <img v-else
            src="./no-cover.gif"
            class="article-cover"
            alt=""> -->
            <!--
              
              
             -->
            <!-- <img
              class="article-cover"
              :src="scope.row.cover.images[0] || './no-cover.gif'" alt=""
            > -->
          </template>
        </el-table-column>
        <el-table-column
          prop="title"
          label="">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-tag :type="articleStatus[scope.row.status].type">{{articleStatus[scope.row.status].text}}</el-tag>
            <!-- <el-tag v-if="scope.row.status === 0"></el-tag>
            <el-tag v-else-if="scope.row.status === 1" type="warning"></el-tag>
            <el-tag v-else-if="scope.row.status === 2" type="success"></el-tag>
            <el-tag v-else-if="scope.row.status === 3" type="danger"></el-tag>
            <el-tag v-else-if="scope.row.status === 4" type="info"></el-tag> -->
          </template>
        </el-table-column>
        <el-table-column
          prop="pubdate"
          label="">
        </el-table-column>
        <el-table-column
          prop="address"
          label="">
          <template slot-scope="scope">
            <el-button
            type="primary"
            icon="el-icon-edit"
            circle
            plain
            @click="$router.push('/publish?id=' + scope.row.id.toString())"
            ></el-button>
            <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            plain
            @click="onDelArticle(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- / -->

      <!--
        total 
         10 
        page-size  .sync  10 
       -->
      <!--  -->
      <el-pagination
        background
        class="article-page"
        layout="prev, pager, next"
        :total="totalCount"
        :page-size="pageSize"
        :disabled="isLoading"
        :current-page.sync="currentPage"
        @current-change="onCurrentChange"
        >
      </el-pagination>
      <!-- / -->
    </el-card>
  </div>
</template>

<script>
import { getArticle, getChannel, delArticle } from '@/api/article'
export default {
  name: 'ArticleIndex',
  props: {},
  components: {},
  data () {
    return {
      form: {
        name: 'ArticleIndex',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      articles: [],
      articleStatus: [
        { status: 0, text: '', type: '' },
        { status: 1, text: '', type: 'warning' },
        { status: 2, text: '', type: 'success' },
        { status: 3, text: '', type: 'danger' },
        { status: 4, text: '', type: 'info' }
      ],
      totalCount: 0, // 
      pageSize: 10, // 
      status: null, // 
      channels: [], // 
      channelId: null, // id
      rangeDate: null, // 
      isLoading: false, // 
      currentPage: 1 // 
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadArticles()
    this.getChannels()
  },
  methods: {
    loadArticles (page = 1) {
      // loading
      this.isLoading = true
      // axios
      getArticle({
        page,
        per_page: this.pageSize,
        status: this.status,
        channel_id: this.channelId,
        begin_pubdate: this.rangeDate ? this.rangeDate[0] : null, // 
        end_pubdate: this.rangeDate ? this.rangeDate[1] : null // 
      }).then(res => {
        const { results, total_count: totalCount } = res.data.data
        this.articles = results
        this.totalCount = totalCount

        // loading
        this.isLoading = false
      })
    },
    onCurrentChange (page) {
      this.loadArticles(page)
    },
    getChannels () {
      getChannel().then(res => {
        this.channels = res.data.data.channels
      })
    },
    onDelArticle (articleId) { // 
      // console.log(articleId)
      // console.log(articleId.toString())
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        delArticle(articleId.toString()).then(res => {
          console.log(res)
          // 
          this.loadArticles(this.currentPage)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  },
  mounted () {}
}
</script>
