<template>
  <div class="upload-cover" @click="openDialog">
    <!-- <el-image
      class="cover-image"
      ref="cover-image"
      :src="$route.query.id ? coverImage : currentImage"
    >
      <div slot="error" class="image-slot">
        <i class="el-icon-picture-outline"></i>
      </div>
    </el-image> -->
    <div class="cover-wrap">
      <img :src="value" ref="cover-image" class="cover-image">
    </div>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      append-to-body
    >
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="" name="first">
          <image-list
            :is-show-add="false"
            :is-show-action="false"
            is-show-selected
            ref="image-list"
          ></image-list>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
         <input
            type="file"
            id="file"
            ref="file"
            hidden
            @change="uploadImageChange"
          >
          <label for="file">
            <img
              id="preview-image"
              ref="preview-image"
            >
          </label>
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="onCoverConfirm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { uploadImage } from '@/api/image'
import ImageList from '@/views/image/components/image-list'

export default {
  name: 'UploadCover',
  // props: ['cover-image'], // 
  props: ['value'],
  components: {
    ImageList
  },
  data () {
    return {
      dialogVisible: false,
      activeName: 'second',
      currentImage: ''
    }
  },
  computed: {},
  watch: {},
  created () {},
  methods: {
    openDialog () {
      this.dialogVisible = true
    },
    uploadImageChange () {
      // 
      const file = this.$refs.file.files[0]
      // 
      const blob = window.URL.createObjectURL(file)
      this.$refs['preview-image'].src = blob
    },
    onCoverConfirm () {
      // tabinput-file
      if (this.activeName === 'second') {
        const file = this.$refs.file.files[0]
        if (!file) {
          this.$message('')
          return
        }
        // 
        const fd = new FormData()
        fd.append('image', file)
        uploadImage(fd).then(res => {
          // console.log(res)
          // 
          this.dialogVisible = false
          // 
          // this.$refs['cover-image'].src = res.data.data.url
          this.currentImage = res.data.data.url
          // 
          // this.$emit('update-cover', res.data.data.url)
          this.$emit('input', res.data.data.url)
        })
      } else if (this.activeName === 'first') {
        const imageList = this.$refs['image-list']
        const selected = imageList.selected
        if (selected === null) {
          this.$message('')
          return
        }
        // 
        this.dialogVisible = false
        // 
        this.$emit('input', imageList.images[selected].url)
      }
    }
  },
  mounted () {}
}
</script>
<style lang='less' scoped>
.cover-wrap {
  width: 150px;
  height: 160px;
  line-height: 160px;
  border: 1px dashed #ccc;
  font-size: 30px;
  color: #ccc;
  text-align: center;
  .cover-image {
    height: 160px;
    max-width: 100%
  }
}
#preview-image {
  width: 150px;
  height: 160px;
}
</style>
