<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item>{{$route.query.id ? '' : ''}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-form
      ref="publish-form"
      :model="article"
      :rules="formRules"
      label-width="60px">
      <el-form-item label="" prop="title">
        <el-input
          v-model="article.title"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="content">
        <!-- 5.element-tiptap -->
        <el-tiptap
         v-model="article.content"
         :extensions="extensions"
         placeholder=""
         height="400"
         ></el-tiptap>
      </el-form-item>
      <el-form-item label="">
        <el-radio-group v-model="article.cover.type">
        <el-radio :label="1"></el-radio>
        <el-radio :label="3"></el-radio>
        <el-radio :label="0"></el-radio>
        <el-radio :label="-1"></el-radio>
        </el-radio-group>
        <div class="upload-main">
          <template v-if="article.cover.type > 0">
            <upload-cover
              v-for="(cover, index) in article.cover.type"
              :key="cover"
              v-model="article.cover.images[index]"
            ></upload-cover>
            <!-- <upload-cover
              v-for="(cover, index) in article.cover.type"
              :key="cover"
              v-model="article.cover.images[index]"
              :cover-image="article.cover.images[index]"
              @update-cover="onUpdateCover(index, $event)"
            ></upload-cover> -->
          </template>
        </div>
      </el-form-item>
      <el-form-item label="" prop="channel_id">
        <el-select
          v-model="article.channel_id"
          placeholder="">
          <el-option
           v-for="(article, index) in channels"
           :key="index"
           :label="article.name"
           :value="article.id"
           ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item v-if="$route.query.id">
        <el-button type="warning" @click="onUpdate(false)"></el-button>
      </el-form-item>
      <el-form-item v-else>
        <el-button type="primary" @click="onPublish(false)"></el-button>
        <el-button @click="onPublish(true)"></el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
import {
  getChannel,
  publishArticle,
  getOneArticle,
  updateArticle
} from '@/api/article'
// 1.
import {
  ElementTiptap,
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  TextColor,
  Image,
  Underline,
  Italic,
  Strike,
  HorizontalRule,
  ListItem,
  BulletList,
  OrderedList,
  TodoItem,
  TodoList,
  CodeBlock,
  Preview,
  Fullscreen
} from 'element-tiptap'
// 2.
import 'element-tiptap/lib/index.css'
import UploadCover from './components/upload-cover'

// 
import { uploadImage } from '@/api/image.js'

export default {
  name: 'PublishIndex',
  props: {},
  components: {
    UploadCover,
    // 3.element-tiptap
    'el-tiptap': ElementTiptap
  },
  data () {
    return {
      channels: [],
      article: {
        title: '',
        content: '',
        cover: {
          type: 1, // 1 3 0 -1 
          images: []
        },
        channel_id: null
      },
      // 4.dataextensions
      //  extensions
      // 
      extensions: [
        new Doc(),
        new Text(), // 
        new Paragraph(), // 
        new Heading({ level: 5 }), // 
        new Bold({ bubble: true }), //  
        new TextColor(), // 
        new Image({
          uploadRequest (file) {
            const fd = new FormData()
            fd.append('image', file)
            return uploadImage(fd).then(res => {
              return res.data.data.url
            })
          }
        }), // 
        new Underline(), // 
        new Italic(), // 
        new Strike(), // 
        new HorizontalRule(), // 
        new ListItem(),
        new BulletList(), // 
        new OrderedList(), // 
        new TodoItem(),
        new TodoList(), // 
        new CodeBlock(), // 
        new Preview(), // 
        new Fullscreen() // 
      ],
      formRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
        ],
        content: [
          {
            validator: (rule, value, callback) => {
              if (value === '<p></p>') {
                callback(new Error(''))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          },
          { required: true, message: '', trigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {
    this.onloadChannels()
    if (this.$route.query.id) {
      // id
      this.loadArcitle()
    }
  },
  methods: {
    onloadChannels () {
      getChannel().then(res => {
        this.channels = res.data.data.channels
      })
    },
    onPublish (draft) {
      // 
      this.$refs['publish-form'].validate(valid => {
        if (!valid) {
          return
        }
        // 
        publishArticle(this.article, draft).then(res => {
          console.log(res)
          this.$message({
            message: `${draft ? '' : ''}`,
            type: 'success'
          })
          // 
          this.$router.push('/article')
        }).catch(err => {
          console.log('' + err)
          this.$message.error('')
        })
      })
    },
    loadArcitle () {
      getOneArticle(this.$route.query.id).then(res => {
        this.article = res.data.data
      })
    },
    onUpdate (draft) {
      updateArticle(this.$route.query.id, this.article, draft).then(res => {
        this.$message({
          message: '',
          type: 'success'
        })
        // 
        this.$router.push('/article')
      }).catch(err => {
        console.log('' + err)
        this.$message.error('')
      })
    },
    onUpdateCover (index, url) {
      console.log(index, url)
      this.article.cover.images[index] = url
    }
  },
  mounted () {}
}
</script>
<style lang='less' scoped>
.upload-main {
  display: flex;
  .upload-cover {
    margin-right: 20px;
  }
}
</style>
