<template>
  <div class="setting-container">
    <el-card class="box-card">
      <!--  -->
      <div slot="header" class="clearfix">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <!-- / -->
      <el-row>
        <!--  -->
        <el-col :span="10">
          <el-form
            ref="form"
            :rules="rules"
            :model="user"
            label-width="90px"
          >
            <el-form-item label=":">
              {{user.id}}
            </el-form-item>
            <el-form-item label=":">
              {{user.mobile}}
            </el-form-item>
            <el-form-item prop="name" label=":">
              <el-input v-model="user.name"></el-input>
            </el-form-item>
            <el-form-item prop="intro" label=":">
              <el-input type="textarea" v-model="user.intro"></el-input>
            </el-form-item>
            <el-form-item prop="email" label=":">
              <el-input v-model="user.email"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                :loading="updataUserLoading"
                @click="onUpdateUserInfo"
              ></el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <!-- / -->
        <!--  -->
        <el-col :span="8" :offset="6">
          <label for="file">
            <el-avatar
              shape="square"
              :size="150"
              fit="cover"
              :src="user.photo"
            ></el-avatar>
          <p></p>
          </label>
          <input
            id="file"
            type="file"
            hidden
            ref="file"
            @change="onFileChange"
          >
        </el-col>
        <!-- / -->
      </el-row>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      append-to-body
      width="30%"
      @opened="onDialogOpend"
      @closed="onDialogClosed"
    >
      <div class="preview-image-wrap">
        <img
          class="preview-image"
          ref="preview-image"
          :src="previewImage"
        >
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="onUpdatePhoto"
          :loading="updatePhotoLoading"
        > </el-button>
      </span>
    </el-dialog>
    <!-- / -->
  </div>
</template>

<script>
import {
  getUserProfile,
  updataUserPhoto,
  updataUserProfile
} from '@/api/user'
// cropperjscssjs
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
// global-bus
import globalBus from '@/utils/global-bus'
export default {
  name: 'SettingIndex',
  props: {},
  components: {},
  data () {
    return {
      user: {
        id: null, // id
        name: '', // 
        intro: '', // 
        photo: '', // 
        email: '', // 
        mobile: '' // 
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/, message: '', trigger: 'blur' }
        ]
      },
      dialogVisible: false, // 
      previewImage: '', // 
      cropper: null, // cropper
      updatePhotoLoading: false, // loading
      updataUserLoading: false // loading
    }
  },
  computed: {},
  watch: {},
  created () {
    this.uploadUserInfo()
  },
  methods: {
    onUpdateUserInfo () {
      // loading
      this.updataUserLoading = true
      // 
      this.$refs.form.validate(valid => {
        // 
        if (!valid) {
          // loading
          this.updataUserLoading = false
          return
        }
        // 
        updataUserProfile(this.user).then(res => {
          // loading
          this.updataUserLoading = false
          this.$message({
            type: 'success',
            message: ''
          })
          // 
          globalBus.$emit('updateUser', this.user)
        }).catch(err => {
          console.log('' + err)
          this.$message({
            type: 'waring',
            message: ''
          })
        })
      })
    },
    // 
    uploadUserInfo () {
      getUserProfile().then(res => {
        this.user = res.data.data
      })
    },
    // 
    onFileChange () {
      const file = this.$refs.file
      // console.dir(file)
      const blobData = window.URL.createObjectURL(file.files[0])
      this.previewImage = blobData
      // 
      this.dialogVisible = true
      // change
      this.$refs.file.value = ''
    },
    onDialogOpend () {
      // DOM
      const image = this.$refs['preview-image']
      // url
      // if (this.cropper) {
      //   this.cropper.replace(this.previewImage)
      // }
      // 
      this.cropper = new Cropper(image, {
        aspectRatio: 1, // 
        viewMode: 1 // 
      // crop
      // crop(event) {
      //   console.log(event.detail.x);
      //   console.log(event.detail.y);
      //   console.log(event.detail.width);
      //   console.log(event.detail.height);
      //   console.log(event.detail.rotate);
      //   console.log(event.detail.scaleX);
      //   console.log(event.detail.scaleY);
      // },
      })
    },
    onDialogClosed () {
      // 
      this.cropper.destroy()
    },
    onUpdatePhoto () {
      // loaging
      this.updatePhotoLoading = true
      // 
      // getCroppedCanvas()HTMLCanvasElementcanvas
      // HTMLCanvasElement.toBlob
      // toBlobimage/png
      this.cropper.getCroppedCanvas().toBlob(file => {
        const fd = new FormData()
        fd.append('photo', file)
        // fd
        updataUserPhoto(fd).then(res => {
          // loading
          this.updatePhotoLoading = false
          // 
          this.dialogVisible = false
          // 
          // this.user.photo = res.data.data.photo
          this.user.photo = window.URL.createObjectURL(file)
          // 
          globalBus.$emit('updateUser', this.user)
        })
      })
    }
  },
  mounted () {}
}
</script>
<style lang='less' scoped>
.preview-image-wrap {
  .preview-image {
    display: block;
    max-width: 100%;
    height: 200px;
  }
}
</style>
