<template>
  <div class="login-container">
    <div class="login-box">
      <el-form class="login-from" ref="login-form" :rules="fromRules" :model="user">
          <img src="./logo_index.png">
          <el-form-item prop="mobile">
              <el-input v-model="user.mobile" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="code">
              <el-input v-model="user.code" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="agree">
              <el-checkbox v-model="user.agree"></el-checkbox>
          </el-form-item>
          <el-form-item>
              <el-button class="login-btn" type="primary" :loading="loginLoading" @click="onLogin"></el-button>
          </el-form-item>
      </el-form>
      </div>
  </div>
</template>

<script>
import { login } from '@/api/user'
export default {
  name: 'LoginIndex',
  data () {
    return {
      user: {
        mobile: '13922222222',
        code: '246810',
        agree: true
      },
      loginLoading: false,
      fromRules: {
        mobile: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^1[35789]\d{9}$/, message: '', trigger: 'change' }
        ],
        code: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^\d{6}$/, message: '', trigger: 'change' }
        ],
        agree: [
          {
            validator: (rule, value, callback) => {
              if (value) {
                callback()
              } else {
                callback(new Error(''))
              }
            },
            trigger: 'change'
          }
        ]
      }
    }
  },
  methods: {
    onLogin () {
      // 
      // const user = this.user
      // 
      this.$refs['login-form'].validate(valid => {
        // 
        if (!valid) {
          return
        }
        // 
        this.login()
      })
    },
    login () {
      // 
      // 
      this.loginLoading = true

      login(this.user).then(res => {
        // console.log(res)
        // 
        this.$message({
          message: '',
          type: 'success'
        })

        // token
        window.localStorage.setItem('user', JSON.stringify(res.data.data))

        //  loading
        this.loginLoading = false

        // 
        this.$router.push({ name: 'home' })
      }).catch(err => {
        console.log('' + err)
        this.$message.error('')

        //  loading
        this.loginLoading = false
      })
    }
  }
}
</script>
<style scoped lang="less">
  .login-container {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background: url('./login_bg.jpg') no-repeat;
      background-size: cover;
      display: flex;
      justify-content: center;
      align-items: center;
      .login-box {
          padding: 0 50px;
          background-color: #fff;
          width: 400px;
          box-sizing: border-box;
          .login-from {
              img {
                  width: 50%;
                  margin: 20px 0 20px 60px;
              }
              .login-btn {
                  width: 100%;;
              }
          }
      }
  }
</style>
